import { Component, AfterContentInit, OnDestroy, AfterViewInit, Output, Input, EventEmitter, ElementRef, TemplateRef, ContentChild, ViewChild, ViewChildren, QueryList, Renderer, ApplicationRef, ViewEncapsulation } from '@angular/core';
import { toObserver, toObservable, bind, defaults } from "@ay/util";
import * as _ from "lodash";
import { Subscription } from "rxjs/Rx";

export type SelectableCondition = (item: any) => boolean;
export type IsSame = (a: any, b: any) => boolean;

export class CheckboxComponent implements AfterContentInit {
  //- 私有變數
  private _itemElementsChange$: Subscription;
  private _itemsChange$: Subscription;

  //- 公有變數
  public queriedItems: any[] = [];
  public isSelectable: boolean[] = [];

  //- 參數
  /** data */
  private _data: any[];

  
  public dataChange: EventEmitter<any[]> = new EventEmitter<any[]>();

  
  public get data(): any[] {
    return this._data;
  }

  public set data(data: any[]) {
    if (this._data === data) return;
    this._data = data;
    this.dataChange.emit(this._data);
  }

  /** items */
  private _items: toObservable<any[]>;

  public itemsChange: EventEmitter<toObservable<any[]>> = new EventEmitter<toObservable<any[]>>();

  
  public get items(): toObservable<any[]> {
    return this._items;
  }

  public set items(menu: toObservable<any[]>) {
    if (this._items === menu) return;
    this._items = menu;
    this.itemsChange.emit(this._items);
  }

  /** isChecked */
  private _isChecked: boolean[];

  
  public isCheckedChange: EventEmitter<boolean[]> = new EventEmitter<boolean[]>();

  
  public get isChecked(): boolean[] {
    return this._isChecked;
  }

  public set isChecked(isChecked: boolean[]) {
    if (this._isChecked === isChecked) return;
    this._isChecked = isChecked;
    this.isCheckedChange.emit(this._isChecked);
  }

  /** isMulti */
  private _isMulti: number = 1;

  
  public isMultiChange: EventEmitter<number> = new EventEmitter<number>();

  
  public get multi(): number {
    return this._isMulti;
  }

  public set multi(isMulti: number) {
    if (this._isMulti === isMulti) return;
    this._isMulti = isMulti;
    this.isMultiChange.emit(this._isMulti);
  }

  /** isLoading */
  private _isLoading: boolean;

  
  public isLoadingChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  public get isLoading(): boolean {
    return this._isLoading;
  }

  public set isLoading(isLoading: boolean) {
    if (this._isLoading === isLoading) return;
    this._isLoading = isLoading;
    this.isLoadingChange.emit(this._isLoading);
  }

  /** selectableCondition */
  private _selectableCondition: SelectableCondition =
  (item) => true;

  public selectableConditionChange: EventEmitter<SelectableCondition> = new EventEmitter<SelectableCondition>();

  
  public get selectableCondition(): SelectableCondition {
    return this._selectableCondition;
  }

  public set selectableCondition(selectableCondition: SelectableCondition) {
    if (this._selectableCondition === selectableCondition) return;
    this._selectableCondition = selectableCondition;
    this.selectableConditionChange.emit(this._selectableCondition);
  }

  /** isSame */
  private _isSame: IsSame = (a, b) => a === b;

  
  public isSameChange: EventEmitter<IsSame> = new EventEmitter<IsSame>();

  
  public get isSame(): IsSame {
    return this._isSame;
  }

  public set isSame(isSame: IsSame) {
    if (this._isSame === isSame) return;
    this._isSame = isSame;
    this.isSameChange.emit(this._isSame);
  }

  //- 元素
  
  public itemElements: QueryList<ElementRef> = null;

  //- 樣板
  
  public defaultItemTemplate: ElementRef = null;

  
  public itemTemplate: TemplateRef<any> = null;

  
  public defaultCheckboxTemplate: ElementRef = null;

  
  public checkboxTemplate: TemplateRef<any> = null;

  //- 事件
  public constructor(public renderer: Renderer, public appRef: ApplicationRef) { }

  public ngAfterContentInit(): void {
    this.itemTemplate = defaults(this.itemTemplate, this.defaultItemTemplate);
    this.checkboxTemplate = defaults(this.checkboxTemplate, this.defaultCheckboxTemplate);
    this._itemsChange$ = this.itemsChange.subscribe(this.afterItemsChange, this.onError);
    this.afterItemsChange();
  }

  public ngAfterViewInit() {
    this._itemElementsChange$ = this.itemElements.changes.subscribe(this.updateItemWidth);
    this.itemElements.notifyOnChanges();
  }

  public ngOnDestroy() {
    this._itemsChange$.unsubscribe();
    this._itemElementsChange$.unsubscribe();
  }

  @bind
  public afterItemsChange() {
    let selectedItems = this.data;
    this.queriedItems = [];
    this.isLoading = true;
    toObserver(this.items)
      .subscribe(items => {
        this.isLoading = false;
        this.queriedItems = this.queriedItems.concat(items);
        this.data = _.intersection(this.queriedItems, selectedItems);
        this.recalcIsChecked();
        this.recalcSelectable();
      });
  }

  @bind
  public recalcSelectable() {
    this.isSelectable = this.queriedItems.map(item => this.selectableCondition(item));
  }

  @bind
  public updateItemWidth() {
    let maxWidth = this.itemElements.reduce((prev, item) => Math.max(item.nativeElement.offsetWidth, prev), 100) + 10;
    this.itemElements.map(item => this.renderer.setElementStyle(item.nativeElement, "width", maxWidth + 'px '));
  }

  @bind
  public onError(err) {
    console.error(err);
  }

  //- 功能函數

  @bind
  public toggle(index: number) {
    if (!this.isSelectable[index]) return;
    if (!this.isChecked[index] && this.data.length >= this.multi) return;
    this.isChecked[index] = !this.isChecked[index];
    if (this.isChecked[index]) {
      this.data.push(this.queriedItems[index]);
    } else {
      let idx = this.data.indexOf(this.queriedItems[index]);
      this.data.splice(idx, 1);
    }
  }

  @bind
  public recalcIsChecked() {
    this.isChecked = this.queriedItems.map(item => this.data.indexOf(item) !== -1);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ay-checkbox',
  template: `
    <ng-template [ngForOf]="queriedItems" let-i="index" let-item="" ngFor=""><button #itemElements="" [ngClass]="{ disable: !isSelectable[i] }" (click)="toggle(i)" tabindex="1"><div class="box" [ngClass]="{ checked: isChecked[i] }"></div><div class="label">{{ item }}</div></button></ng-template>
  `,
  styles: [`
    ay-checkbox {
      display: inline-block;
      cursor: pointer;
      vertical-align: top; }
      ay-checkbox .box {
        display: inline-block;
        position: relative;
        border: 1px solid #E2E2E2;
        border-radius: 3px;
        width: 1em;
        height: 1em;
        margin: .25em .5em .25em 0; }
        ay-checkbox .box:after {
          display: block;
          position: absolute;
          top: .5em;
          left: .1em;
          transform: scaleX(-1) rotate(135deg);
          transform-origin: left top;
          border-right: 0.2em solid #308BC8;
          border-top: 0.2em solid #308BC8;
          content: '';
          animation: ay-checkbox-off .2s linear;
          height: .5em;
          width: .25em;
          opacity: 0; }
        ay-checkbox .box.checked:after {
          opacity: 1;
          animation: ay-checkbox-on .2s linear; }
      ay-checkbox button {
        text-align: left;
        margin-right: 2em;
        display: inline-block;
        padding: .25em 0em;
        background-color: transparent;
        border: none;
        cursor: pointer;
        line-height: 1.5em;
        box-shadow: border-box; }
        ay-checkbox button .label {
          display: inline-block;
          vertical-align: top;
          line-height: 1.5em;
          height: 1.5em;
          color: #666; }
        ay-checkbox button:focus {
          outline: none; }
          ay-checkbox button:focus .box {
            box-shadow: 0 1px 7px rgba(0, 0, 0, 0.05), 0 7px 17px rgba(0, 0, 0, 0.1); }
          ay-checkbox button:focus .label {
            color: #000; }
        ay-checkbox button.disable .box {
          background-color: #E2E2E2;
          box-shadow: none; }
        ay-checkbox button.disable .label {
          color: #666; }

    @keyframes ay-checkbox-off {
      100% {
        opacity: 0; }
      50% { }
      0% {
        opacity: 1; } }

    @keyframes ay-checkbox-on {
      0% {
        width: 0;
        height: 0; }
      50% {
        width: .25em;
        height: 0; }
      100% {
        height: .5em; } }
  `],
  encapsulation: ViewEncapsulation.None
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Renderer, },
{type: ApplicationRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'dataChange': [{ type: Output },],
'data': [{ type: Input },],
'items': [{ type: Input },],
'isCheckedChange': [{ type: Output },],
'isChecked': [{ type: Input },],
'isMultiChange': [{ type: Output },],
'multi': [{ type: Input, args: ["multi", ] },],
'isLoadingChange': [{ type: Output },],
'selectableCondition': [{ type: Input, args: ["selectable", ] },],
'isSameChange': [{ type: Output },],
'isSame': [{ type: Input },],
'itemElements': [{ type: ViewChildren, args: ["itemElements", ] },],
'defaultItemTemplate': [{ type: ViewChild, args: ["defaultItemTemplate", ] },],
'itemTemplate': [{ type: ContentChild, args: ["itemTemplate", ] },],
'defaultCheckboxTemplate': [{ type: ViewChild, args: ["defaultCheckboxTemplate", ] },],
'checkboxTemplate': [{ type: ContentChild, args: ["checkboxTemplate", ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
